.advantages {
	min-height: 473px;
	background: #F6F6F6;
	padding: 73px 0 65px 0;
	&__elements {
		margin-top: 60px;
		min-height: 245px;
		display: flex;
		align-items: center;
		justify-content: space-between;
}
	&__element {
		max-width: 235px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-direction: column;
	}
	&__descr {
		margin-top: 35px;
		font-weight: 300;
		font-size: 14px;
		line-height: 18px;
		text-align: center;
		color: #0D0D0D;
		h3 {
			margin-bottom: 20px;
			font-weight: 700;
			font-size: 18px;
			line-height: 29px;
		}
		a {
			color: #FF0000;
		}
	}
	&__icon {
		&_animated_heartbeat {
			animation: heartbeat 1.5s infinite ease;
			&:hover {
				animation-play-state: paused;
			}
		}
		&_animated_shake {
			&:hover {
				animation: shake 0.1s 3 linear;
			}
			
		}
	}
}